<template>
  <div :class="`${prefixCls}__container`" class="overflow-hidden">
    <TopNav />
    <HomeFullPage>
      <!-- <swiper direction="vertical" :mousewheel="true" ref="mySwiper">
        <swiper-slide><Overview /></swiper-slide>
        <swiper-slide><About /></swiper-slide>
        <swiper-slide><Choise /></swiper-slide>
        <swiper-slide><RBTtoken /></swiper-slide>
        <swiper-slide><Partner /></swiper-slide>
      </swiper> -->
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide"><Overview /></div>
          <div class="swiper-slide"><About /></div>
          <div class="swiper-slide"><Choise /></div>
          <div class="swiper-slide"><RBTtoken /></div>
          <div class="swiper-slide"><Partner /></div>
        </div>
      </div>
    </HomeFullPage>
  </div>
</template>
<script lang="ts">
  import { defineComponent, onMounted } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import TopNav from './TopNav.vue';
  import Overview from './Overview.vue';
  import About from './About.vue';
  import Choise from './Choise.vue';
  import RBTtoken from './RBTtoken.vue';
  import Partner from './Partner.vue';
  import Swiper from 'swiper';
  import '@/design/home.css';
  // import SwiperCore, { Mousewheel } from 'swiper';
  // import { Swiper, SwiperSlide } from 'swiper/vue';
  // import 'swiper/swiper.scss';
  // SwiperCore.use([Mousewheel]);
  // import 'swiper/dist/css/swiper.min.css';
  // import 'swiper/dist/js/swiper.min.js';
  import { swiperAnimateCache, swiperAnimate } from '@/helper/swiper.animate';
  import '@/design/animate.css';
  export default defineComponent({
    name: 'container',
    components: {
      TopNav,
      Overview,
      About,
      Choise,
      RBTtoken,
      Partner,
      // Swiper,
      // SwiperSlide,
    },
    setup() {
      const { prefixCls } = useDesign('home');
      onMounted(() => {
        new Swiper('.swiper-container', {
          direction: 'vertical',
          mousewheel: true,
          on: {
            init: function () {
              //隐藏动画元素
              swiperAnimateCache(this);
              //初始化完成开始动画
              swiperAnimate(this);
            },
            slideChangeTransitionEnd: function () {
              //每个slide切换结束时也运行当前slide动画
              swiperAnimate(this);
            },
          },
        });
      });
      return { prefixCls };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-home';
  ::v-deep(.bg-color) {
    background: rgba(#fff, 0.03);
  }
  ::v-deep(.swiper-container) {
    width: 100%;
    @apply h-screen;
    .swiper-slide {
      @apply flex justify-center items-center;
    }
  }
  .@{prefix-cls} {
    @apply text-white text-sm;
    &__container {
      @apply w-full h-screen;
    }
  }
</style>
