function clearSwiperAnimate(obj) {
  for (let j = 0; j < obj.slides.length; j++) {
    const allBoxes = obj.slides[j].querySelectorAll('.ani');
    for (let i = 0; i < allBoxes.length; i++) {
      if (allBoxes[i].attributes['swiper-animate-style-cache']) {
        allBoxes[i].setAttribute(
          'style',
          allBoxes[i].attributes['swiper-animate-style-cache'].value
        );
      }
      allBoxes[i].style.visibility = 'hidden';
      allBoxes[i].className = allBoxes[i].className.replace('animated', ' ');
      if (allBoxes[i].attributes['swiper-animate-effect']) {
        const effect = allBoxes[i].attributes['swiper-animate-effect'].value;
        allBoxes[i].className = allBoxes[i].className.replace(effect, ' ');
      }
    }
  }
}
export function swiperAnimateCache(obj) {
  for (let j = 0; j < obj.slides.length; j++) {
    const allBoxes = obj.slides[j].querySelectorAll('.ani');
    for (let i = 0; i < allBoxes.length; i++) {
      allBoxes[i].attributes['style']
        ? allBoxes[i].setAttribute(
            'swiper-animate-style-cache',
            allBoxes[i].attributes['style'].value
          )
        : allBoxes[i].setAttribute('swiper-animate-style-cache', ' '),
        (allBoxes[i].style.visibility = 'hidden');
    }
  }
}

export function swiperAnimate(obj) {
  clearSwiperAnimate(obj);
  const allBoxes = obj.slides[obj.activeIndex].querySelectorAll('.ani');
  for (let i = 0; i < allBoxes.length; i++) {
    allBoxes[i].style.visibility = 'visible';
    let style = allBoxes[i].attributes['style'].value;
    const effect = allBoxes[i].attributes['swiper-animate-effect']
      ? allBoxes[i].attributes['swiper-animate-effect'].value
      : '';
    allBoxes[i].className = allBoxes[i].className + ' ' + effect + ' ' + 'animated';
    const duration = allBoxes[i].attributes['swiper-animate-duration']
      ? allBoxes[i].attributes['swiper-animate-duration'].value
      : '';
    if (duration) {
      style =
        style + 'animation-duration:' + duration + ';-webkit-animation-duration:' + duration + ';';
    }
    const delay = allBoxes[i].attributes['swiper-animate-delay']
      ? allBoxes[i].attributes['swiper-animate-delay'].value
      : '';
    if (delay) {
      style = style + 'animation-delay:' + delay + ';-webkit-animation-delay:' + delay + ';';
    }
    allBoxes[i].setAttribute('style', style);
  }
}
